import React, { useState } from 'react';
import {
    ListGroup,
    ListGroupItem,
    Row,
    Col,
    FormInput,
    FormSelect,
    Card,
    CardHeader,
    CardBody
  } from "shards-react";

function ColumnFootingDetails(props) {
    const [columnType, setColumnType] = useState('rectangular');
    const [footingType, setFootingType] = useState('rectangular');

    const [columnRectangular, setColumnRectangular] = useState({x: null, y: null, z: null});
    const [columnCircular, setColumnCircular] = useState({d: null, z: null});

    const [footingRectangular, setFootingReactangular] = useState({x: null, y: null, z: null});
    const [footingCircular, setFootingCircular] = useState({d: null, z: null});

    const [columnCount, setColumnCount] = useState();

    return (
        <Card small className="mb-4">
            <CardHeader className="border-bottom">
                <h6 className="m-0">Column Footing Details</h6>

                {/* <button className="btn-add-header" onClick={() => {props.incrementColumnFootingFormCount()}}><i className="material-icons">add</i></button> */}
            </CardHeader>
            <CardBody className="p-0 pb-3">
                <ListGroup flush>
                    <ListGroupItem className="p-3">
                        <Row>
                            <Col>
                                <Row form>
                                    <Col md="12" className="form-group">
                                        <label htmlFor="ew-cfd-type-of-column">Type of Column</label>
                                        <FormSelect onChange={e => setColumnType(e.target.value)} id="ew-cfd-type-of-column">
                                            <option value="rectangular">Rectangular</option>
                                            <option value="circular">Circular</option>
                                        </FormSelect>
                                    </Col>
                                </Row>

                                { columnType === 'rectangular' &&
                                    <Row form>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-column-rectangular-x">Length (x)</label>
                                            <FormInput onChange={e => setColumnRectangular(object => {object.x = e.target.value; return object;})} value={columnRectangular.x} id="ew-cfd-column-rectangular-x" placeholder="Enter the value x" />
                                        </Col>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-column-rectangular-y">Width (y)</label>
                                            <FormInput onChange={e => setColumnRectangular(object => {object.y = e.target.value; return object;})} value={columnRectangular.y} id="ew-cfd-column-rectangular-y" placeholder="Enter the value y" />
                                        </Col>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-column-rectangular-z">Depth (z)</label>
                                            <FormInput onChange={e => setColumnRectangular(object => {object.z = e.target.value; return object;})} value={columnRectangular.z} id="ew-cfd-column-rectangular-z" placeholder="Enter the value z" />
                                        </Col>
                                    </Row>
                                }

                                { columnType === 'circular' &&
                                    <Row form>
                                        <Col xxsm="6" className="form-group">
                                        <label htmlFor="ew-cfd-circular-d">Diameter (d)</label>
                                            <FormInput onChange={e => setColumnCircular(object => {object.d = e.target.value; return object;})} value={columnCircular.d} id="ew-cfd-column-circular-d" placeholder="Enter the value d" />
                                        </Col>
                                        <Col xxsm="6" className="form-group">
                                        <label htmlFor="ew-cfd-column-circular-z">Depth (z)</label>
                                            <FormInput onChange={e => setColumnCircular(object => {object.z = e.target.value; return object;})} value={columnCircular.z} id="ew-cfd-circular-circular-z" placeholder="Enter the value z" />
                                        </Col>
                                    </Row>
                                }

                                <Row form>
                                    <Col md="12" className="form-group">
                                        <label htmlFor="ew-cfd-type-of-footing">Type of Footing</label>
                                        <FormSelect onChange={e => setFootingType(e.target.value)} id="ew-cfd-type-of-footing">
                                            <option value="rectangular">Rectangular</option>
                                            <option value="circular">Circular</option>
                                        </FormSelect>
                                    </Col>
                                </Row>

                                { footingType === 'rectangular' &&
                                    <Row form>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-footing-rectangular-x">Length (x)</label>
                                            <FormInput onChange={e => setFootingReactangular(object => {object.x = e.target.value; return object;})} value={footingRectangular.x} id="ew-cfd-footing-rectangular-x" placeholder="Enter the value x" />
                                        </Col>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-footing-rectangular-y">Width (y)</label>
                                            <FormInput onChange={e => setFootingReactangular(object => {object.y = e.target.value; return object;})} value={footingRectangular.y} id="ew-cfd-footing-rectangular-y" placeholder="Enter the value y" />
                                        </Col>
                                        <Col xxsm="4" className="form-group">
                                            <label htmlFor="ew-cfd-footing-rectangular-z">Depth (z)</label>
                                            <FormInput onChange={e => setFootingReactangular(object => {object.z = e.target.value; return object;})} value={footingRectangular.z} id="ew-cfd-footing-rectangular-z" placeholder="Enter the value z" />
                                        </Col>
                                    </Row>
                                }

                                { footingType === 'circular' &&
                                    <Row form>
                                        <Col xxsm="6" className="form-group">
                                            <label htmlFor="ew-cfd-footing-circular-d">Diameter (d)</label>
                                            <FormInput onChange={e => setFootingCircular(object => {object.d = e.target.value; return object;})} value={footingCircular.d} id="ew-cfd-footing-circular-d" placeholder="Enter the value d" />
                                        </Col>
                                        <Col xxsm="6" className="form-group">
                                            <label htmlFor="ew-cfd-footing-circular-z">Depth (z)</label>
                                            <FormInput onChange={e => setFootingCircular(object => {object.z = e.target.value; return object;})} value={footingCircular.z} id="ew-cfd-footing-circular-z" placeholder="Enter the value z" />
                                        </Col>
                                    </Row>
                                }

                                <Row form>
                                    <Col xxsm="6" className="form-group">
                                        <label htmlFor="ew-cfd-column-count">Number of Columns</label>
                                        <FormInput onChange={e => setColumnCount(e.target.value)} value={columnCount} type="number" id="ew-cfd-column-count" placeholder="Enter number of columns" />
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </ListGroupItem>
                </ListGroup>
            </CardBody>
        </Card>
    );
}

export default ColumnFootingDetails;